$white: #fff;
$black: #07021B;
$red: #E9484A;
$grey: #A2A1AF;
$violet: #4E4A65;
$pink: #FFD6D6;

$h1: 56px;
$h2: 48px;
$h3: 40px;
$h4: 32px;
$h5: 24px;
$h6: 16px;
$h7: 14px;


body {

  margin: 0;
  font-size: $h7;
  font-family: 'Lato', sans-serif;
  color: $black;
  background: #EBEBED;
}

* {
  box-sizing: border-box;
}

a {
  color: $black;
  transition: .2s all;
}



/*Header*/
.header_fixed,
header {
  display: grid;
  grid-template-columns: max-content auto max-content max-content;
}

.basket_button {
  padding: 26px 40px;
  background: $pink;
}

.logo_image {
  padding: 16px 48px;
}

.ui.bottom.popup.popup_all.buyer_popup {
  margin-left: -17px;
}

.help_footer {
  position: relative;

  &:hover {
    .phone_help_popup {
      opacity: 1;
    }

  }
}

.phone_help_popup {
  opacity: 0;
  transition: .5s all;
  position: absolute;
  background: white;
  padding: 62px 32px 32px;
  top: -2px;
  border: 2px solid #07021B;
  box-sizing: border-box;
  width: calc(100% + 4px);
  left: -2px;
}

.ui.bottom.popup.popup_all,
.ui.bottom.popup.phone_popup {
  top: 0 !important;
  z-index: 2;
  padding-top: 48px;
  border-radius: 0;
  border: 1px solid #EBEBED;
  box-sizing: border-box;

  &:before {
    display: none;
  }
}

.phone_popup_text {
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
}

.phone_popup_text:hover {
  text-decoration: underline;
}

.ui.form input:not([type]):focus, .ui.form input[type=date]:focus, .ui.form input[type=datetime-local]:focus, .ui.form input[type=email]:focus, .ui.form input[type=file]:focus, .ui.form input[type=number]:focus, .ui.form input[type=password]:focus, .ui.form input[type=search]:focus, .ui.form input[type=tel]:focus, .ui.form input[type=text]:focus, .ui.form input[type=time]:focus, .ui.form input[type=url]:focus {
  border-radius: 0;
  background: rgba(235, 235, 237, .5);
}

.header_measurer.entrance_text {
  margin-bottom: 104px;
}

.ui.form .field > label.entrance_label {
  margin-top: 16px;
  font-size: $h7;
  line-height: $h6;
  font-weight: normal;
  color: #4E4A65;
}

.entrance_button {
  text-align: center;
  margin-top: 64px;
}

.ui.selection.dropdown .menu > .item {
  font-size: $h7;
  line-height: $h7;
}

.ui.default.dropdown:not(.button) > .text, .ui.dropdown:not(.button) > .default.text {
  color: $black;
}

.header_measurer.call_text {
  margin-bottom: 162px;
}

.form_call button {
  margin-top: 40px;
}

.ui.checkbox input:checked ~ .box:after, .ui.checkbox input:checked ~ label:after {
  content: " ";
  background: $red;
  width: 8px;
  height: 8px;
  top: 4px;
  left: 4px;
}

.ui.checkbox .box:before, .ui.checkbox label:before {
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  border-radius: 0;
  border: 1px solid #07021B !important;
}

.form_date {
  margin: 40px 0 48px;
}

.ui.search.dropdown > .text {
  font-weight: 500;
  font-size: $h7;
  line-height: $h7;
}

.ui.selection.dropdown {
  background: #EBEBED;
  border: none;
  border-bottom: 2px solid #777588;
  border-radius: 0;
}

.ui.form input:not([type]), .ui.form input[type=date], .ui.form input[type=datetime-local], .ui.form input[type=email], .ui.form input[type=file], .ui.form input[type=number], .ui.form input[type=password], .ui.form input[type=search], .ui.form input[type=tel], .ui.form input[type=text], .ui.form input[type=time], .ui.form input[type=url] {
  &::placeholder {
    font-weight: 500;
    font-size: $h7;
    line-height: $h7;
    color: #4E4A65;
  }

  background: #EBEBED;
  border: none;
  border-radius: 0;
  border-bottom: 2px solid #777588;
}

.link_call {
  text-decoration-line: underline;
  color: $red;
  cursor: pointer;
}

.phone_popup {
  p {
    max-width: 122px;
    color: #4E4A65;
    display: flex;
    margin-top: 8px;
  }

  span {
    font-weight: bold;
    color: $black;
  }
}

.ui.bottom.popup.phone_popup {
  width: 100%;
  max-width: 183px;
}

.menu_list_grid .icon_all {
  &.active {
    &:before {
      content: " ";
      background: white;
      top: 100%;
      height: 37px;
      width: 100%;
      position: absolute;
    }
  }
}

.icon_all {
  &.active {


    .hover_list {
      pointer-events: auto;
      opacity: 1;
      z-index: 3;
    }

    .icon {
      transform: rotate(180deg);
    }
  }

  .icon:before {
    display: none;
  }

  .icon {
    transition: .5s all;
    width: 10px;
    height: 6px;
    justify-content: center;
    align-items: center;
    display: flex;
    padding: 0;
    top: 8px;
    margin: 0;
    position: absolute;
    right: 0;
    background: url("../assets/img/svg/arrow_drop.svg") no-repeat 100% center;
  }
}

.buyer_popup_header .phone_popup_text {
  font-size: $h6;
  line-height: $h6;
}

.phone_help_popup {
  p, span {
    font-size: 14px;
    padding-left: 30px;
  }

  .footer_item_phone {
    margin-bottom: 10px;
    display: flex;
    align-items: center;

    svg {
      width: 20px;
      height: 20px;
    }

    path {
      fill: #4E4A65;
    }

    .footer_item_text {
      font-weight: bold;
      font-size: 16px;
      line-height: 18px;
    }

    color: #07021B;
  }
}


.phone_help_popup_header,
.buyer_popup_header,
.phone_popup_header {
  display: flex;
  padding-right: 18px;
  z-index: 3;
  position: relative;
  cursor: pointer;
  font-weight: bold;
  font-size: $h6;
  line-height: $h6;


}

.basket_items_close {
  width: 40px;
  height: 40px;
  background: #EBEBED;
  display: flex;
  align-items: center;
  justify-content: center;
}

.basket_items_close svg rect {
  fill: $red;
}

i.icon.close:before {
  content: " ";
  background-size: 100%;
  width: 12px;
  height: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url("../assets/img/svg/close.svg") no-repeat center !important;

}

.ui.modal > .close {
  padding: 0;
  right: 0;
  top: 0;
  opacity: 1;
  width: 44px;
  margin: 0;
  height: 44px;
  background: #4E4A65;
}

.submit {
  padding: 20px 56px;
  background: #E9484A;
  border: 0;
  margin-bottom: 20px;
  width: 100%;
}

.ui.selection.dropdown.all_dropdown {
  .dropdown.icon:before {
    display: none;
  }

  .dropdown.icon {
    width: 10px;
    height: 6px;
    justify-content: center;
    align-items: center;
    display: flex;
    padding: 0;
    top: 8px;
    margin: 0;
    right: 0;
    background: url("../assets/img/svg/arrow_drop.svg") no-repeat 100% center;
  }
}

.ui.selection.dropdown.all_dropdown {
  .text:not(.default) {
    font-weight: bold;
  }

  box-shadow: none;
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
  background: none;

  .dropdown.icon {
    transition: .5s all
  }

  &.active {
    .dropdown.icon {
      transform: rotate(180deg);
    }
  }

  .menu > .item:hover {
    background: none;
  }


  border: none;
  width: max-content;
  min-width: initial;
  padding: 0 18px 0 0;
  height: max-content;
  min-height: max-content;

  .menu {
    background: #FFFFFF !important;
    z-index: -1;
    width: 79px;
    padding: 54px 17px 20px;
    overflow: hidden;
    background: none;
    top: -20px;
    left: 50%;
    transform: translate(-50%, 0);
    border: 1px solid #EBEBED !important;
    border-radius: 0;
    box-shadow: none;
  }


  .text {
    font-size: $h6;
    line-height: $h6;
  }


  .active.item {
    display: none;
  }

  .menu > .item:last-child {
    margin-bottom: 0;
  }


  .menu > .item {
    padding: 0 !important;
  }

  .menu > .item > a {
    font-size: $h6;
    line-height: $h6;
  }

  .menu .item.default {
    display: none;
  }

}

.ui.input.search_city {
  input {
    border-radius: 0;
    border: 1px solid #4E4A65;
    padding: 18px 16px;
  }

  width: 100%;

}

.city_button {
  .ui.button {
    &:hover {
      color: $red;
    }

    background: none;
    padding: 0;
    font-weight: normal;
    font-size: $h6;
    text-decoration: underline;
    line-height: $h6;
  }
}

.ui.popup.city_popup {
  max-width: 372px;
  padding: 32px;
  box-shadow: none;
  border-radius: 0;
}

.content_city {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 24px;
  grid-gap: 20px;
}

.content_city .content_city_text {
  font-weight: normal;
  font-size: $h7;
  line-height: $h7;

  &:hover {
    cursor: pointer;
    color: $red;
  }
}


.city_popup_text {
  font-weight: bold;
  font-size: $h6;
  line-height: $h6;
  margin-bottom: 24px;
}

.button_all {
  cursor: pointer;
  font-weight: bold;
  font-size: $h6;
  line-height: $h6;
  color: $white;
  text-transform: uppercase;
  border: none;

}

.header_info {
  width: 100%;
  background: #EBEBED;
  height: 100%;
  align-items: center;
  padding: 24px 40px 24px 72px;
  display: grid;
  grid-template-columns: repeat(6, max-content );
  justify-content: space-between;
  max-width: 1266px;
}

.social_link {
  display: grid;
  grid-gap: 12px;
  margin: 20px 0;
}

.social_link_item {
  display: flex;
  align-items: center;

  .social_img {
    margin-right: 8px;
  }
}

.text_bold_s {
  font-size: $h6;
  line-height: $h6;
  font-weight: bold;
  text-transform: none;
  color: $black;
}

a:hover {
  color: $red;
}

.buyer_popup_link {
  a:hover {
    color: $red;
  }
}

.button_header {
  padding: 28px 48px;
  display: flex;
  align-items: center;
  background: $violet;

}

.ui.dimmer {
  background-color: rgba(7, 2, 27, .25);
}

.icon_img {
  cursor: pointer;
  position: relative;
  display: flex;

  :hover path {
    fill: $red;
  }

  * {

    width: 20px;
    height: 20px;
  }

  .label_icon {
    &.gray {
      background: $grey;
    }

    font-weight: bold;
    color: #FFFFFF;
    font-size: 11px;
    line-height: 10px;
    width: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20px;
    position: relative;
    background: $red;
    border-radius: 20px;
    right: 2px;
  }
}

.menu_list_grid {
  display: grid;
  grid-template-columns: repeat(5, max-content);
}

.menu_list_grid {
  width: 100%;
  max-width: 1234px;
  display: grid;
  justify-content: space-between;
}

.menu_list_grid {
  li {
    position: relative;
    cursor: pointer;
    padding-right: 18px;

    a {
      text-transform: uppercase;
      font-weight: bold;
      font-size: $h6;
      line-height: $h6;
    }
  }
}

.menu_list {
  width: 100%;
}

.header_bottom {
  background: white;

  &.active:after {
    z-index: 20;
    content: " ";
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(7, 2, 27, 0.25);
  }

  padding: 19px 48px;
  display: flex;
  align-items: center;
}

.search_header_b {
  width: 100%;
  max-width: 350px;
}

.ui.search.search_header_b input::placeholder {
  font-size: $h6;
  line-height: $h6;
  color: #4E4A65;
}

.container_all {
  padding: 0 116px;
}

.ui.icon.input .search.icon {
  background-size: 100%;
  background: url("../assets/img/svg/Search.svg") no-repeat center;
  width: 20px;
  height: 20px;
  top: 50%;
  opacity: 1;
  transform: translate(0, -50%);
}

.search.icon:before {
  display: none;
}

.ui.search.search_header_b input {
  border: none;
  border-radius: 0;
  padding: 8px;
  width: 100%;

  border-bottom: 1px solid #07021B;;
}

.icon_container {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  align-items: center;
  grid-gap: 40px;
}

.ui.dimmer,
.scrolling.dimmable.dimmed > .dimmer {
  padding: 0 !important;
  overflow: hidden;
}

.buyer_popup_link {
  display: grid;
  grid-gap: 12px;
}

.ui.modal.left_modal.modal_basket {
  padding: 64px;
  max-width: 680px;
}

.ui.modal.left_modal {
  margin: 0 !important;
}

.basket_bottom {
  display: flex;
  justify-content: space-between;
}

.basket_total {
  font-size: 20px;
  line-height: 20px;

  text-transform: uppercase;
  color: #4E4A65;
}

.modal_basket_b {
  max-width: 360px;
  margin: 0 auto;
  width: 100%;
}

.header_measurer {
  font-weight: bold;
  font-size: $h4;
  line-height: $h4;
  margin-bottom: 64px;
}

.header_date {
  font-weight: bold;
  font-size: $h6;
  line-height: $h6;
  margin-bottom: 24px;
}

.flex_container {
  display: flex;
  height: 100%;
  flex-direction: column;
  position: relative;
}

.password_info {
  display: flex;
  justify-content: space-between;
}

.link_underline {
  font-size: $h7;
  line-height: $h7;
  text-decoration-line: underline;
  cursor: pointer;

}

.password_info {
  margin-top: 16px;
  margin-bottom: 24px;
}

.cod_message > p {
  margin-bottom: 14px;
}

.input_message {
  input {
    padding: 0;
    border-bottom: 0 !important;
    text-align: center;
  }

  display: grid;
  grid-template-columns: repeat(4, 50px);
