body {
  margin: 0;
  font-size: 14px;
  font-family: 'Lato', sans-serif;
  color: #07021B;
  background: #EBEBED;
}

* {
  box-sizing: border-box;
}

a {
  color: #07021B;
  transition: .2s all;
}

/*Header*/
.header_fixed,
header {
  display: grid;
  grid-template-columns: max-content auto max-content max-content;
}

.basket_button {
  padding: 26px 40px;
  background: #FFD6D6;
}

.logo_image {
  padding: 16px 48px;
}

.ui.bottom.popup.popup_all.buyer_popup {
  margin-left: -17px;
}

.help_footer {
  position: relative;
}

.help_footer:hover .phone_help_popup {
  opacity: 1;
}

.phone_help_popup {
  opacity: 0;
  transition: .5s all;
  position: absolute;
  background: white;
  padding: 62px 32px 32px;
  top: -2px;
  border: 2px solid #07021B;
  box-sizing: border-box;
  width: calc(100% + 4px);
  left: -2px;
}

.ui.bottom.popup.popup_all,
.ui.bottom.popup.phone_popup {
  top: 0 !important;
  z-index: 2;
  padding-top: 48px;
  border-radius: 0;
  border: 1px solid #EBEBED;
  box-sizing: border-box;
}

.ui.bottom.popup.popup_all:before,
.ui.bottom.popup.phone_popup:before {
  display: none;
}

.phone_popup_text {
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
}

.phone_popup_text:hover {
  text-decoration: underline;
}

.ui.form input:not([type]):focus, .ui.form input[type=date]:focus, .ui.form input[type=datetime-local]:focus, .ui.form input[type=email]:focus, .ui.form input[type=file]:focus, .ui.form input[type=number]:focus, .ui.form input[type=password]:focus, .ui.form input[type=search]:focus, .ui.form input[type=tel]:focus, .ui.form input[type=text]:focus, .ui.form input[type=time]:focus, .ui.form input[type=url]:focus {
